export const VUE_APP_BASE_URL = process.env.VUE_APP_BASE_URL;

export const USERNAME_REGEXP = /^\S+$/;
export const PASSWORD_REGEXP = /^[a-zA-Z0-9!()-.?[\]_`~#$^&*+=]+$/;

export const USER_NAME_LEN_MAX = Number.parseInt(
  process.env.VUE_APP_USER_NAME_LEN_MAX
);
export const USER_NAME_LEN_MIN = Number.parseInt(
  process.env.VUE_APP_USER_NAME_LEN_MIN
);
export const PASSWORD_LEN_MAX = Number.parseInt(
  process.env.VUE_APP_PASSWORD_LEN_MAX
);
export const PASSWORD_LEN_MIN = Number.parseInt(
  process.env.VUE_APP_PASSWORD_LEN_MIN
);
