import { DEFAULT_ALERT_OPTIONS } from "./options";

export default {
  render(createElement) {
    if (this.$scopedSlots.default) {
      return this.$scopedSlots.default({
        options: this.options,
        show: this.show
      });
    } else {
      return createElement();
    }
  },
  model: {
    prop: "show",
    event: "showChange"
  },
  props: {
    show: {
      type: Boolean,
      default: false
    }
  },
  data: () => ({
    options: {
      ...DEFAULT_ALERT_OPTIONS
    }
  }),
  created() {
    this._$alertListener = info => {
      if (!info) {
        this.$emit("showChange", false);
      } else {
        this.options = info;
        this.$emit("showChange", true);
      }

      this.emitAlert(info);
    };

    this.$onAlert(this._$alertListener);
  },

  beforeDestroy() {
    this.$offAlert(this._$alertListener);
  },

  methods: {
    emitAlert(...args) {
      this.$emit("alert", ...args);
    }
  }
};
