import { makeRestFly } from "../fly";
import { processError } from "./api-error";
const rest = makeRestFly("user");

export async function login(info) {
  try {
    const { data } = await rest.post("login", info);
    return data;
  } catch (error) {
    processError(error);
  }
}

export async function register(info) {
  try {
    const { data } = await rest.post("register", info);
    return data;
  } catch (error) {
    processError(error);
  }
}
