<template>
  <img v-if="user && user.avatar" :src="user.avatar" />
  <span v-else-if="user" class="white--text headline">{{
    user.name && user.name[0] && user.name[0].toUpperCase()
  }}</span>
  <v-icon v-else>fa-user-slash</v-icon>
</template>
<script>
import { queryMe, getUserById } from "../store/user";

export default {
  props: {
    /**
     * if userId is falsy or "me",
     * then the avatar of current user will be displayed.
     */
    userId: String
  },

  computed: {
    me: queryMe,

    user() {
      return this.userId && this.userId !== "me"
        ? getUserById(this.userId)
        : this.me;
    }
  }
};
</script>
