import { getStoreBuilder } from "vuex-typex";
import { getPersistence } from "./persist";

const storeBuilder = getStoreBuilder();

export const MODULE_NAME = "theme";

const moduleBuilder = storeBuilder.module(MODULE_NAME, {
  isDark: false
});

const getState = moduleBuilder.state();

export const queryIsDark = () => getState().isDark;

export const switchDarkMode = moduleBuilder.commit((state, dark) => {
  if (typeof dark === "boolean") {
    state.isDark = dark;
  } else {
    state.isDark = !state.isDark;
  }
}, "switchDarkMode");

// export function persist() {
//   return getPersistence(MODULE_NAME);
// }

export const persistPlugin = getPersistence(MODULE_NAME).plugin;
