export const DEFAULT_ALERT_OPTIONS = {
  color: undefined,
  dismissible: true,
  timeout: 3000,
  icon: undefined,
  outline: false,
  type: "error",
  message: ""
};

export function getAlertOptions(options) {
  if (!options) {
    options = {};
  }

  if (typeof options === "string") {
    options = { type: "info", message: options };
  }

  return {
    ...DEFAULT_ALERT_OPTIONS,
    ...options
  };
}

export function getAlertOptionsFromError(error, otherOptions) {
  return getAlertOptions({
    message: error && error.message,
    type: "error",
    ...otherOptions
  });
}
