export class RestFly {
  setFlyInstance(/** @type {import("flyio").Fly} */ fly) {
    this.fly = fly;
    return this;
  }

  constructor(...paths) {
    this.path = paths.join("/") + "/";
  }

  getUrl(url) {
    return this.path + url;
  }
}

["request", "get", "post", "put", "patch", "head", "delete"].map(function(
  method
) {
  RestFly.prototype[method] = function(url, data, config) {
    return this.fly[method](this.getUrl(url), data, config);
  };
});
