import * as userApi from "../utils/api/user";

import { getStoreBuilder } from "vuex-typex";
import { getPersistence } from "./persist";

const storeBuilder = getStoreBuilder();

export const MODULE_NAME = "user";

const moduleBuilder = storeBuilder.module(MODULE_NAME, {
  otherUsers: [],
  me: null
});

const getState = moduleBuilder.state();

export const queryMe = () => {
  return getState().me;
};

const getUserByIdGetter = moduleBuilder.read(
  state => id => {
    if (!id) return null;
    return id === getMyId()
      ? state.me
      : state.otherUsers.filter(u => u.id === id) || null;
  },
  "getUserById"
);

export const getUserById = (...args) => getUserByIdGetter()(...args);

export const getUsers = moduleBuilder.read(
  state => (state.me ? state.otherUsers.concat(state.me) : state.otherUsers),
  "getUsers"
);

export const getMyId = moduleBuilder.read(state => {
  return state.me && state.me.id;
}, "getMyId");

export const getJwtToken = moduleBuilder.read(state => {
  return state.me && state.me.token;
}, "getJwtToken");

export const setMe = moduleBuilder.commit((state, me) => {
  state.me = me;
}, "setMe");

export const setJwtToken = moduleBuilder.commit((state, jwtToken) => {
  if (!state.me) {
    state.me = {};
  }

  state.me.jwtToken = jwtToken;
}, "setJwtToken");

export const logout = moduleBuilder.commit(state => {
  state.me = null;
  state.user = [];
}, "logout");

export const login = moduleBuilder.dispatch(async (ctx, userLoginInfo) => {
  const res = await userApi.login(userLoginInfo);
  setMe(res);
  return res;
}, "login");

export const register = moduleBuilder.dispatch(
  async (ctx, userRegisterInfo) => {
    const res = await userApi.register(userRegisterInfo);
    setMe(res);
    return res;
  },
  "register"
);

export const persistPlugin = getPersistence(MODULE_NAME).plugin;
