import { getAlertOptionsFromError, getAlertOptions } from "./options";
import AlertWatcher from "./alert-watcher";

export class AlertPlugin {
  static install(
    /** @type {import('vue').VueConstructor} */
    Vue,
    options
  ) {
    const alert = new AlertPlugin(options);

    Vue.alertManager = alert;

    // https://vuetifyjs.com/en/components/alerts#api
    Vue.prototype.$alert = (...args) => {
      alert.alert(...args);
    };

    Vue.prototype.$onAlert = function(...args) {
      alert.on(...args);
    };

    Vue.prototype.$offAlert = function(...args) {
      alert.off(...args);
    };

    Vue.prototype.$alertError = function(...args) {
      alert.error(...args);
    };

    Vue.prototype.$alertWarning = function(...args) {
      alert.warn(...args);
    };

    Vue.prototype.$alertInfo = function(...args) {
      alert.info(...args);
    };

    Vue.prototype.$alertSuccess = function(...args) {
      alert.success(...args);
    };

    Vue.component("alert-watcher", AlertWatcher);

    return alert;
  }

  listeners = [];

  constructor(options) {
    this.options = options;
  }

  alert(alertOptions) {
    this.listeners.forEach(lsn =>
      lsn(alertOptions && getAlertOptions(alertOptions))
    );
  }

  error(error, otherOptions) {
    this.alert(getAlertOptionsFromError(error, otherOptions));
  }

  warn(error, otherOptions) {
    this.alert(
      getAlertOptionsFromError(error, { type: "warning", ...otherOptions })
    );
  }

  info(message, otherOptions) {
    this.alert({ type: "info", message, ...otherOptions });
  }

  success(message, otherOptions) {
    this.alert({ type: "success", message, ...otherOptions });
  }

  on(listener) {
    if (typeof listener === "function") {
      this.listeners.push(listener);
    }
  }

  off(listener) {
    const i = this.listeners.indexOf(listener);
    if (i >= 0) {
      this.listeners.splice(i, 1);
    }
  }
}
