import VuexPersistence from "vuex-persist";

export const getPersistence = moduleName =>
  new VuexPersistence({
    key: `vuex[${moduleName}]`,
    reducer: state => ({
      ...state[moduleName]
    }),
    restoreState(key, storage) {
      try {
        const json = storage.getItem(key);
        if (json) {
          const obj = JSON.parse(json);
          return { [moduleName]: obj };
        } else {
          return null;
        }
      } catch (e) {
        return null;
      }
    },
    storage: localStorage
  });
