import { getStoreBuilder } from "vuex-typex";
import { navs, bottomNavs } from "./navs";

const storeBuilder = getStoreBuilder();

export const MODULE_NAME = "view";

const moduleBuilder = storeBuilder.module(MODULE_NAME, {
  navs,
  bottomNavs
});

const getState = moduleBuilder.state();

export const queryNavs = () => {
  return getState().navs;
};

export const queryBottomNavs = () => {
  return getState().bottomNavs;
};
