import "./styles.styl";

import debounce from "lodash.debounce";

const updateVh = () => {
  const fullVh = window.innerHeight;
  const vh = fullVh / 100;
  document.documentElement.style.setProperty("--vh", `${vh}px`);
  document.documentElement.style.setProperty("--fvh", `${fullVh}px`);
};

updateVh();

window.addEventListener("resize", debounce(updateVh, 50, { maxWait: 100 }));
